


* {
  color: white;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

body {
  background-color: rgb(0, 0, 0);
}

.AuthScreen {
  display: flex;
  justify-content: center;
  align-items: center;

}

.auth-box {
  margin-top: 3cm;
  width: 17cm;
  // border: 1px solid white;
  border-radius: 10px;
  background-color: #0f0f13;
  padding: .3cm .6cm;
}

.welcome {
 margin-bottom: -.45cm;
}

.tagline {
  font-size: 65px;
  line-height: 1.75cm;
  // font-weight: 600;
  color: rgb(243, 243, 243);
  font-family:Tilt Neon;
}

.sub-tagline {
  font-size: 25px;
  margin-top: .3cm;
  color: rgb(96, 96, 96);
  font-family:Tilt Neon;
}

.google-button{
  margin-top: 1.3cm;
  margin-bottom: .5cm;
  background-color: transparent;
  color: white;
  border: 3px solid #2d2d2d;
  padding: .3cm .7cm;
  font-family:Tilt Neon;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: .25s;

  &:hover{
    background-color: #2d2d2d;
    
  }
}
.Nav{
  padding: .3cm 10vw;
  background-color: #0f0f13;
}
.name{
  font-family: Tilt Neon;
  font-size: 30px;
}
.setPassword{
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(10px);
 
 
}
.saveU{
  width: 100%;
  background-color: transparent;
  color: white;
  border: 3px solid #2d2d2d;
  padding: .3cm .7cm;
  font-family:Tilt Neon;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: .25s;

  &:hover{
    background-color: #2d2d2d;
    
  }
  
}
.user-form{
  display: flex;
  row-gap: .3cm;
  flex-direction: column;
  align-items: center;
  width: 100%;
  justify-content: center;
}
.user-input {
  background-color: transparent;
  color: white;
  font-family: Tilt Neon;
  border: 3px solid #2d2d2d;
  font-size: 18px;
  padding: 0.3cm 0.2cm; /* Adjusted padding values */
  width: calc(100% - 0.6cm); /* Adjusted width to account for padding */
  border-radius: 10px;
}

.username-box{
  display: flex;
  width: 10cm;
  row-gap: .3cm;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0f0f13;
  padding: .0cm .5cm .5cm .5cm;
  border-radius: 10px;
}
.menu-item{
  cursor: pointer;
  font-family: Tilt neon;
  font-size: 16px;
}
.menu{
  display: flex;
  column-gap: .5cm;
  align-items: center;
}
.nav-box{
  display: flex;
  justify-content: space-between;
  align-items: center;
}